import React from "react";
import Link from "next/link";
import * as motion from "framer-motion/client";

// Components
import ProductCard from "./ProductCard";

// UI Components
import {
  Carousel,
  CarouselContent,
  CarouselItem,
} from "@/components/ui/carousel";

// Utils & Types
import { cn } from "@/lib/utils";
import { Product } from "@/lib/types/product.types";

// Styles
import { integralCF } from "@/styles/fonts";

type ProductListSecProps = {
  title: string;
  description?: string;
  data: Product[];
  viewAllLink?: string;
};

const ProductListSec = ({
  title,
  description,
  data,
  viewAllLink,
}: ProductListSecProps) => {
  return (
    <section className="max-w-frame mx-auto text-center">
      <motion.h2
        initial={{ y: "100px", opacity: 0 }}
        whileInView={{ y: "0", opacity: 1 }}
        viewport={{ once: true }}
        transition={{ duration: 0.6 }}
        className={cn([
          integralCF.className,
          `text-[32px] md:text-5xl capitalize ${
            description ? "mb-2 md:mb-4" : "mb-8 md:mb-14"
          }`,
        ])}
      >
        {title}
      </motion.h2>

      {description && (
        <motion.p
          initial={{ y: "100px", opacity: 0 }}
          whileInView={{ y: "0", opacity: 1 }}
          viewport={{ once: true }}
          transition={{ delay: 0.3, duration: 0.6 }}
          className="text-sm md:text-base text-gray-600 mb-8 md:mb-14"
        >
          {description}
        </motion.p>
      )}
      <motion.div
        initial={{ y: "100px", opacity: 0 }}
        whileInView={{ y: "0", opacity: 1 }}
        viewport={{ once: true }}
        transition={{ delay: 0.6, duration: 0.6 }}
      >
        <Carousel
          opts={{
            align: "start",
          }}
          className="w-full mb-6 md:mb-9"
        >
          <CarouselContent className="mx-4 xl:mx-0 space-x-4 sm:space-x-5">
            {data.map((product) => (
              <CarouselItem
                key={product.id}
                className="w-full max-w-[198px] sm:max-w-[295px] pl-0"
              >
                <ProductCard data={product} />
              </CarouselItem>
            ))}
          </CarouselContent>
        </Carousel>
        {viewAllLink && (
          <div className="w-full px-4 sm:px-0 text-center">
            <Link
              href={viewAllLink}
              className="w-full inline-block sm:w-[218px] px-[54px] py-4 border rounded-full hover:bg-black hover:text-white text-black transition-all font-medium text-sm sm:text-base border-black/10"
            >
              Voir tout
            </Link>
          </div>
        )}
      </motion.div>
    </section>
  );
};

export default ProductListSec;
